import article from '@/view/article/'
import story from '@/view/story/'
import thus from '@/view/thus/'
import login from '@/view/login/'
import manage from '@/view/manage/'
import dashboard from '@/view/dashboard/'
import initStore from '@/view/initStore/'
import feature from '@/view/feature/'
import product from '@/view/product/'

/**
 * @meta: auth:  012 showheaderBar 
 */
let routes = [
  {
    path: "/",
    redirect: "/login"
  },
  {
    path: "/login",
    component: login,
    meta: {
      title: " - ",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/initStore",
    name: "initStore",
    component: initStore,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/uxo",
    name: "uxo",
    component: story,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/story",
    name: "story",
    component: story,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/product",
    name: "product",
    component: product,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: false
    }
  },
  {
    path: "/article",
    name: "article",
    component: article,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: dashboard,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: '/feature',
    name: 'feature',
    component: feature,
    meta: {
      title: '',
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/thus",
    name: "thus",
    component: thus,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  },
  {
    path: "/manage",
    name: "manage",
    component: manage,
    meta: {
      title: "",
      auth: 0,
      showheaderBar: true
    }
  }
];

export default routes;



